import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import HeaderButton from '@vkontakte/vkui/dist/components/HeaderButton/HeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';



const osName = platform();

const Prog19 = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<HeaderButton onClick={props.go} data-to="program">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</HeaderButton>}
		>
			
		</PanelHeader>
		<div className="container_p">

		<div className="cont1_p">
		<h1 className="yprag_p" align="center"> : 3  6</h1>
		<div className="chill_p"><h1 className="chl">:3 </h1></div>
		</div>


		<div className="cont2_p">
		<h1 className="yprag_p" align="center">: 3  10</h1>
		<div className="chill_p"><h1 className="chl">:3 </h1></div>
		</div>

		<div className="cont3_p">
		<h1 className="yprag_p" align="center">: 4  15</h1>
		<div className="chill_p"><h1 className="chl">:2 </h1></div>
		</div>

		<div className="cont4_p">
		<h1 className="yprag_p" align="center">: 2  1 </h1>
		<div className="chill_p"><h1 className="chl">:2 </h1></div>
		</div>

		<div className="cont5_p">
		<h1 className="yprag_p" align="center">: 4  45 </h1>
		<div className="chill_p"><h1 className="chl">:2 </h1></div>
		</div>

		<div className="cont6_p">
		<h1 className="yprag_p" align="center"> : 3  9</h1>
		<div className="chill_p"><h1 className="chl">:3 </h1></div>
		</div>

		<div className="cont7_p" align="center">
		<h1 className="yprag_p"> 4  1.5 </h1>
		<div className="chill_p"><h1 className="chl">:3 </h1></div>
		</div>

		</div>	


		</Panel>
);

Prog19.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default Prog19;
